<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" append-icon="mdi-plus" v-bind="activatorProps"
        >Novo Período</v-btn
      >
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" width="450">
        <v-card-title class="bg-success">{{
          steps[step].title
        }}</v-card-title>
        <v-window v-model="step">
          <v-window-item :value="0">
            <v-card-text>
              <v-row justify="center">
                <v-col cols="10"
                  ><v-form v-model="steps[0].formValid">
                    <!-- <span class="text-subtitle-1 text-medium-emphasis">
                  Tipo de Período*
                </span> -->
                    <v-autocomplete
                      v-model="period.type"
                      class="mt-4"
                      :items="periodTypes"
                      label="Tipo de Período"
                      placeholder="Selecione um tipo"
                      prepend-inner-icon="mdi-calendar"
                      no-data-text="Nenhum tipo encontrado"
                      item-title="periodtype_name"
                      item-value="periodtype_id"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      clearable
                      :rules="[rules.required]"
                    ></v-autocomplete> </v-form
                ></v-col>
              </v-row>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="1">
            <v-card-text>
              <v-form v-model="steps[1].formValid">
                <v-locale-provider locale="pt">
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        v-model.trim="period.name"
                        label="Nome"
                        placeholder="Insira um nome"
                        prepend-inner-icon="mdi-tag-text-outline"
                        color="secondary"
                        density="compact"
                        variant="outlined"
                        :rules="[rules.required, rules.maxLength]"
                      ></v-text-field>
                    </v-col>

                    <v-col cols="6">
                      <v-date-input
                        v-model="period.startDate"
                        label="Data de início"
                        prepend-icon=""
                        prepend-inner-icon="mdi-calendar-start"
                        color="secondary"
                        density="compact"
                        variant="outlined"
                        clearable
                        :rules="[rules.required]"
                      ></v-date-input
                    ></v-col>
                    <v-col cols="6">
                      <v-date-input
                        v-model="period.endDate"
                        label="Data de fim"
                        prepend-icon=""
                        prepend-inner-icon="mdi-calendar-end"
                        color="secondary"
                        density="compact"
                        variant="outlined"
                        clearable
                        :rules="[rules.required]"
                      ></v-date-input
                    ></v-col>
                    <v-col cols="12">
                      <v-textarea
                        v-model.trim="period.description"
                        label="Descrição"
                        placeholder="Insira uma descrição"
                        prepend-inner-icon="mdi-text-box-outline"
                        color="secondary"
                        density="compact"
                        variant="outlined"
                        :rules="[rules.description]"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-locale-provider>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="2"
            ><v-card-text> <v-form></v-form> </v-card-text
          ></v-window-item>
        </v-window>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < 1"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="secondary"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else-if="step === 1"
            :disabled="!steps[step].formValid"
            color="success"
            variant="flat"
            append-icon="mdi-check"
            :loading="isLoading"
            @click="postPeriod()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "AddPeriod",
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      period: {
        type: null,
        name: null,
        startDate: null,
        endDate: null,
        description: null,
      },
      isLoading: false,
      step: 0,
      steps: [
        {
          title: "Tipo",
          formValid: false,
        },
        {
          title: "Datas",
          formValid: false,
        },
        // {
        //   title: "Detalhes",
        //   formValid: false,
        // },
      ],
      rules: {
        // required: [(v) => !!v || "Obrigatório"],
        // requiredSize: [
        //   (v) => !!v || "Obrigatório",
        //   (v) => (v && v.length <= 50) || "Máximo de 50 caracteres",
        // ],
        // size: [(v) => (v || "").length <= 255 || "Máximo de 255 caracteres"],
        required: (v) => !!v || "Campo obrigatório",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres.",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
      },

      // Period types
      periodTypes: [],
      isLoadingTypes: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    lastStep() {
      return this.steps.length - 1;
    },
  },
  methods: {
    onOpenDialog() {
      this.getPeriodTypes();
    },
    async getPeriodTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/periodtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            periodtype_id: this.periodTypeSelected,
          },
        })
        .then((response) => {
          this.periodTypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de período");
          this.isLoadingTypes = false;
        });
    },
    async postPeriod() {
      this.isLoading = true;
      let payload = {
        periodtype_id: this.period.type,
        period_name: this.period.name,
        period_description: this.period.description,
        period_startdate: this.period.startDate,
        period_enddate: this.period.endDate,
      };
      await axios
        .request({
          url: "api/periods",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Período adicionado com sucesso");
          this.$emit("refresh");
          this.dialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao adicionar período");
          this.isLoading = false;
        });
    },
    reset() {
      this.period = {
        type: null,
        name: null,
        startDate: null,
        endDate: null,
      };
      this.isLoading = false;
      this.step = 0;
      this.periodTypes = [];
    },
  },
};
</script>

<style></style>
