<template>
  <v-sheet>
    <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-linear color="secondary" indeterminate></v-progress-linear>
      </v-col>
    </v-row>
    <v-row v-else-if="!isLoading && this.region != null" no-gutters>
      <v-col cols="6" class="d-flex flex-column justify-space-evenly">
        <div class="d-flex flex-column justify-space-evenly">
          <span class="text-subtitle-1"
            >Nome: <b>{{ region.region_name }}</b></span
          >
          <span class="text-subtitle-1"
            >Código:
            <v-chip color="secondary">{{ region.region_code }} </v-chip></span
          >
          <span class="text-subtitle-1">
            Localidade: <b>{{ region.localidade }}</b>
          </span>
          <span class="text-subtitle-1">
            Cultura: <b>{{ region.crop?.crop_name || "Sem cultura" }}</b>
          </span>
          <div>
            <span class="text-subtitle-1 d-flex" full-width>
              <div v-if="region.closest_datasource">
                <a
                  class="text-secondary text-decoration-none cursor-pointer"
                  @click="$refs.RegionMap.flyToStation"
                  >{{ region.closest_datasource?.datasource_name }}</a
                >
                <StationData
                  :station_id="region.closest_datasource.datasource_id"
                ></StationData>
              </div>
              <span v-else> <b>Sem estação</b></span>
            </span>
          </div>
        </div>
      </v-col>
      <v-col cols="6">
        <v-row>
          <v-col cols="12">
            <div>
              <RegionMap
                ref="RegionMap"
                :region="region"
                :zoom="15"
                height="300px"
              ></RegionMap>
            </div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import RegionMap from "../Regions/RegionMap.vue";
import StationData from "../WeatherStations/StationData.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "PeriodRegionDetail",
  components: { RegionMap, StationData },
  props: {
    regionId: {
      type: Number,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      region: null,
      isLoading: false,
    };
  },

  methods: {
    async getRegion() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/regions/" + this.regionId,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.region = response.data;
          console.log(this.region);
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    reset() {
      this.region = null;
      this.isLoading = false;
    },
  },
  mounted() {
    this.getRegion();
  },
  unmounted() {
    this.reset();
  },
};
</script>

<style></style>
