<template>
  <v-navigation-drawer
    location="right"
    v-model="drawer"
    width="1000"
    temporary
    disable-route-watcher
  >
    <v-card class="fill-height">
      <v-card-title class="bg-primary">
        Detalhes do Período <i>{{ period?.period_name }}</i>
      </v-card-title>
      <v-card-text class="fill-height pa-0">
        <v-container class="fill-height pa-0">
          <v-row v-if="isLoading" align="center">
            <v-col class="d-flex align-center justify-center">
              <v-progress-circular
                color="secondary"
                size="65"
                width="7"
                indeterminate
              ></v-progress-circular>
            </v-col>
          </v-row>
          <v-card
            v-if="!isLoading && period != null"
            elevation="0"
            flat
            rounded
            width="100%"
            height="100%"
            class="ma-0"
          >
            <v-tabs color="secondary" v-model="tab" grow>
              <v-tab value="period">Período</v-tab>
              <v-tab value="regions">Parcelas</v-tab>
            </v-tabs>
            <v-card-text class="fill-height">
              <v-tabs-window v-model="tab" class="fill-height">
                <v-tabs-window-item value="period" class="fill-height">
                  <PeriodDetail :period="period"></PeriodDetail>
                </v-tabs-window-item>

                <v-tabs-window-item value="regions" class="fill-height">
                  <PeriodRegions :period="period"></PeriodRegions>
                </v-tabs-window-item>
              </v-tabs-window>
            </v-card-text>
          </v-card>
        </v-container>
      </v-card-text>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
import PeriodDetail from "./PeriodDetail.vue";
import PeriodRegions from "./PeriodRegions.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "PeriodDrawer",
  components: {
    PeriodDetail,
    PeriodRegions,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      drawer: false,
      period: null,
      isLoading: false,
      tab: null,
    };
  },
  watch: {
    drawer(value) {
      if (!value) {
        this.reset();
      }
    },
  },
  methods: {
    openDrawer(period_id) {
      this.drawer = true;
      this.getPeriodDetail(period_id);
    },
    reset() {
      this.period = null;
      this.regions = [];
      this.tab = null;
    },
    async getPeriodDetail(period_id) {
      this.isLoading = true;
      //   await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/periods/" + period_id,
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.period = response.data;
          this.period.period_startdate = new Date(this.period.period_startdate);
          this.period.period_enddate = new Date(this.period.period_enddate);
          console.log(this.period);
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar detalhes do período");
          console.error(error);
        });
    },
  },
};
</script>

<style></style>
