<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" width="450">
      <v-card-title class="bg-secondary">{{ steps[step].title }}</v-card-title>
      <v-card-text v-if="isLoading">
        <div class="text-center">
          <v-progress-circular
            indeterminate
            color="primary"
          ></v-progress-circular>
        </div>
      </v-card-text>
      <v-window v-else v-model="step">
        <v-window-item :value="0">
          <v-card-text>
            <v-form v-model="steps[0].formValid">
              <v-locale-provider locale="pt">
                <v-row>
                  <v-col cols="12">
                    <v-autocomplete
                      v-model="period.periodtype_id"
                      class="mt-4"
                      :items="periodTypes"
                      label="Tipo de Período"
                      placeholder="Selecione um tipo"
                      prepend-inner-icon="mdi-calendar"
                      no-data-text="Nenhum tipo encontrado"
                      item-title="periodtype_name"
                      item-value="periodtype_id"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required]"
                    ></v-autocomplete>
                  </v-col>
                  <v-divider></v-divider>
                  <v-col cols="12">
                    <v-text-field
                      class="mt-4"
                      v-model.trim="period.period_name"
                      label="Nome"
                      placeholder="Insira um nome"
                      prepend-inner-icon="mdi-tag-text-outline"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required, rules.sizesmall]"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-date-input
                      v-model="period.period_startdate"
                      label="Data de início"
                      prepend-icon=""
                      prepend-inner-icon="mdi-calendar-start"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      clearable
                      :rules="[rules.required]"
                    ></v-date-input
                  ></v-col>
                  <v-col cols="6">
                    <v-date-input
                      v-model="period.period_enddate"
                      label="Data de fim"
                      prepend-icon=""
                      prepend-inner-icon="mdi-calendar-end"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      clearable
                      :rules="[rules.required]"
                    ></v-date-input
                  ></v-col>
                  <v-col cols="12">
                    <v-textarea
                      v-model.trim="period.period_description"
                      label="Descrição"
                      placeholder="Insira uma descrição"
                      prepend-inner-icon="mdi-text-box-outline"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.description]"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-locale-provider>
            </v-form>
          </v-card-text>
        </v-window-item>
        <v-window-item :value="1">
          <v-card-text>
            <v-row align="center">
              <v-col cols="2">
                <v-btn color="info" variant="outline" icon @click="selectAll">
                  <v-icon>mdi-playlist-check</v-icon>
                  <v-tooltip activator="parent" location="left">Selecionar todas</v-tooltip>
                </v-btn>
              </v-col>
              <v-col cols="10">
                <v-autocomplete
                  v-model="regionsToAdd"
                  :items="regions"
                  label="Parcelas a manter"
                  placeholder="Selecione as parcelas a manter"
                  no-data-text="Nenhuma parcela encontrada"
                  color="secondary"
                  density="compact"
                  variant="outlined"
                  chips
                  :item-title="
                    (item) =>
                      `[${item.region.region_code}] ` + item.region.region_name
                  "
                  closable-chips
                  return-object
                  multiple
                  clearable
                  hide-details
                  prepend-inner-icon="mdi-land-plots-marker"
                >
                  <template v-slot:chip="{ props, item }">
                    <v-chip
                      v-bind="props"
                      :text="
                        `[${item.raw.region.region_code}] ` +
                        item.raw.region.region_name +
                        (item.raw.region.eois
                          ? ' (' + item.raw.region.eois.length + ')'
                          : '')
                      "
                    ></v-chip>
                  </template>
                  <template v-slot:item="{ props, item }">
                    <v-list-item
                      v-bind="props"
                      :title="
                        `[${item.raw.region.region_code}] ` +
                        item.raw.region.region_name
                      "
                      :subtitle="`${
                        item.raw.eois ? item.raw.eois.length : 'Sem'
                      } EOIs`"
                    >
                    </v-list-item>
                  </template>
                </v-autocomplete>
              </v-col>
            </v-row>
            <!-- <v-card class="pa-2" elevation="0" border rounded="lg">
              <v-card-title class="d-flex align-center pl-2">
                <v-btn
                  color="secondary"
                  variant="text"
                  icon
                  @click="selectAll()"
                >
                  <v-icon>mdi-playlist-check</v-icon>
                  <v-tooltip activator="parent" location="bottom"
                    >Selecionar todas</v-tooltip
                  > </v-btn
                ><span>Selecione as parcelas a manter</span>
              </v-card-title>
              <v-card-text class="scrollable-card">
                <div v-for="region in regions" :key="region.regioninperiod_id">
                  <v-checkbox
                    v-model="regionsToAdd"
                    :label="
                      region.region.region_name +
                      (region.eois ? ' (' + region.eois.length + ')' : '')
                    "
                    :value="region"
                    color="secondary"
                    density="compact"
                    hide-details
                  ></v-checkbox>
                </div>
              </v-card-text>
            </v-card> -->
          </v-card-text>
        </v-window-item>
      </v-window>
      <v-divider></v-divider>
      <v-card-actions>
        <v-btn
          v-if="step > 0"
          prepend-icon="mdi-chevron-left"
          variant="tonal"
          @click="step--"
        >
          Anterior
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn
          v-if="step < lastStep"
          :disabled="!steps[step].formValid"
          append-icon="mdi-chevron-right"
          color="secondary"
          variant="tonal"
          @click="step++"
        >
          Próximo
        </v-btn>
        <v-btn
          v-else-if="step === lastStep"
          :disabled="!steps[step].formValid"
          color="success"
          variant="flat"
          append-icon="mdi-check"
          :loading="isLoading || isLoadingPost"
          @click="duplicatePeriod()"
        >
          Adicionar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "PeriodDuplicate",
  props: {
    period_id: {
      type: Number,
    },
    dialog: {
      type: Boolean,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoading: false,
      isLoadingPost: false,
      period: {},
      regionsToAdd: [],
      step: 0,
      steps: [
        {
          title: "Detalhes do Período",
          formValid: false,
        },
        {
          title: "Parcelas a manter",
          formValid: true,
        },
      ],
      rules: {
        required: (v) => !!v || "Campo obrigatório",
        sizesmall: (v) => (v && v.length <= 50) || "Máximo de 50 caracteres",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
      },

      periodTypes: [],
      isLoadingTypes: false,

      regions: [],
      isLoadingRegions: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
    lastStep() {
      return this.steps.length - 1;
    },
    treeItems() {
      return this.regions.map((region) => {
        return {
          id: region.region.region_id,
          name: region.region.region_name,
          children: region.region.eois.map((eoi) => {
            return {
              id: eoi.eoi_id,
              name: eoi.eoi_name,
            };
          }),
        };
      });
    },
  },
  methods: {
    reset() {
      this.isLoading = false;
      this.period = {};
      this.regionsToAdd = [];
      this.periodTypes = [];
      this.regions = [];
      this.step = 0;
    },
    onOpenDialog() {
      // this.getPeriod();
      // this.getPeriodTypes();
      // this.getRegions();
      this.isLoading = true;
      Promise.all([
        this.getPeriod(),
        this.getPeriodTypes(),
        this.getRegions(),
      ]).then(() => {
        this.isLoading = false;
      });
    },
    selectAll() {
      this.regionsToAdd = this.regions;
    },
    async duplicatePeriod() {
      this.isLoadingPost = true;
      let payload = {
        period: this.period,
        regions: this.regionsToAdd.map((region) => {
          return {
            region_id: region.region.region_id,
            eois: region.eois?.map((eoi) => eoi.eoi_id),
          };
        }),
      };
      console.log(payload);
      await axios
        .request({
          url: "api/periods/duplicate",
          method: "POST",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingPost = false;
          this.generalStore.successSnackbar("Período duplicado com sucesso");
          this.localDialog = false;
          this.$emit("refreshPeriods");
        })
        .catch((error) => {
          this.isLoadingPost = false;
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao duplicar período");
        });
    },
    async getPeriod() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/periods/" + this.period_id,
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.period = response.data;
          this.period.period_name = `${this.period.period_name} (Cópia)`;
          this.period.period_startdate = new Date(this.period.period_startdate);
          this.period.period_enddate = new Date(this.period.period_enddate);
        })
        .catch((error) => {
          this.generalStore.errorSnackbar("Erro ao buscar detalhes do período");
          console.error(error);
        });
    },
    async getPeriodTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/periodtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            periodtype_id: this.periodTypeSelected,
          },
        })
        .then((response) => {
          this.periodTypes = response.data;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de período");
        });
    },

    async getRegions() {
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/periods/" + this.period_id + "/regionsinperiods",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          let _regions = response.data.regioninperiods.map((regioninperiod) => {
            return {
              regioninperiod_id: regioninperiod.regioninperiod_id,
              region: regioninperiod.region,
              eois: regioninperiod.elements_of_interest,
            };
          });
          this.regions.push(..._regions);
          this.offset += this.regions.length;
          this.hasMore = response.data.hasMore;
        })
        .catch((error) => {
          this.generalStore.errorSnackbar("Erro ao buscar regiões do período");
        });
    },
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 400px;
  overflow: auto;
}
</style>
