<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" width="450">
      <v-card-title class="bg-warning d-flex align-center"
        ><v-icon size="small" class="mr-2">mdi-pencil</v-icon>
        <span>Editar Período</span></v-card-title
      >
      <v-card-text>
        <v-form v-model="formValid">
          <v-row>
            <v-col cols="12">
              <v-autocomplete
                v-model="localPeriod.periodtype_id"
                class="mt-4"
                :items="periodTypes"
                label="Tipo de Período"
                placeholder="Selecione um tipo"
                prepend-inner-icon="mdi-calendar"
                no-data-text="Nenhum tipo encontrado"
                item-title="periodtype_name"
                item-value="periodtype_id"
                color="secondary"
                density="compact"
                variant="outlined"
                :rules="[rules.required]"
              ></v-autocomplete>
            </v-col>
            <v-divider></v-divider>
            <v-col cols="12">
              <v-text-field
                class="mt-4"
                v-model.trim="localPeriod.period_name"
                label="Nome"
                placeholder="Insira um nome"
                prepend-inner-icon="mdi-tag-text-outline"
                color="secondary"
                density="compact"
                variant="outlined"
                :rules="[rules.required, rules.sizesmall]"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-date-input
                v-model="localPeriod.period_startdate"
                label="Data de início"
                prepend-icon=""
                prepend-inner-icon="mdi-calendar-start"
                color="secondary"
                density="compact"
                variant="outlined"
                clearable
                :rules="[rules.required]"
              ></v-date-input
            ></v-col>
            <v-col cols="6">
              <v-date-input
                v-model="localPeriod.period_enddate"
                label="Data de fim"
                prepend-icon=""
                prepend-inner-icon="mdi-calendar-end"
                color="secondary"
                density="compact"
                variant="outlined"
                clearable
                :rules="[rules.required]"
              ></v-date-input
            ></v-col>
            <v-col cols="12">
              <v-textarea
                v-model.trim="localPeriod.period_description"
                label="Descrição"
                placeholder="Insira uma descrição"
                prepend-inner-icon="mdi-text-box-outline"
                color="secondary"
                density="compact"
                variant="outlined"
                :rules="[rules.description]"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoading"
          :disabled="!formValid"
          @click="patchPeriod()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import { useDate } from "vuetify";

import axios from "axios";

export default {
  name: "PeriodEdit",
  props: {
    period: Object,
    dialog: Boolean,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    const date = useDate();
    return { generalStore, accountStore, date };
  },
  data() {
    return {
      isLoading: false,
      formValid: false,

      rules: {
        required: (v) => !!v || "Campo obrigatório",
        sizesmall: (v) => (v && v.length <= 50) || "Máximo de 50 caracteres",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
      },

      periodTypes: [],
      isLoadingTypes: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.localPeriod.period_startdate = new Date(
          this.localPeriod.period_startdate
        );
        this.localPeriod.period_enddate = new Date(
          this.localPeriod.period_enddate
        );
        console.log(this.localPeriod);
        this.getPeriodTypes();
      }
    },
  },
  computed: {
    localPeriod: {
      get() {
        return this.period;
      },
      set(value) {
        this.$emit("update:period", value);
      },
    },
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
  },
  methods: {
    async patchPeriod() {
      this.isLoading = true;
      axios
        .request({
          url: "api/periods/" + this.period.period_id,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: this.localPeriod,
        })
        .then(() => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Período atualizado com sucesso");
          this.$emit("refreshPeriods");
          this.localDialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao atualizar período");
          this.isLoading = false;
        });
    },
    async getPeriodTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/periodtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            periodtype_id: this.periodTypeSelected,
          },
        })
        .then((response) => {
          this.periodTypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de período");
          this.isLoadingTypes = false;
        });
    },
    reset() {
      this.localPeriod = null;
      this.localDialog = false;
    },
  },
};
</script>

<style></style>
