<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg" width="100%">
    <v-data-table-virtual
      fixed-header
      no-data-text="Nenhum período encontrado"
      loading-text="A carregar períodos..."
      :headers="headers"
      :items="tableItems"
      :loading="isLoadingPeriods"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              color="info"
              variant="text"
              icon="mdi-information-outline"
              @click="showInfo(row.item.period_id)"
            ></v-btn>
          </td>
          <td>
            {{ row.item.period_name }}
          </td>
          <td>
            <v-chip label color="info">{{ row.item.periodtype_name }}</v-chip>
          </td>
          <td>
            {{ formatDate(row.item.period_startdate) }}
          </td>
          <td>
            {{ formatDate(row.item.period_enddate) }}
          </td>
          <td>
            <v-btn
              v-if="row.item.period_description"
              color="info"
              variant="text"
              icon="mdi-text-box-outline"
              @click="showDescription(row.item)"
            ></v-btn>
            <v-btn v-else color="info" variant="text" icon disabled>
              <v-icon>mdi-text-box-outline</v-icon>
            </v-btn>
          </td>
          <td v-if="accountStore.hasRole(['Administrador'])">
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="editPeriod(row.item)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
            </v-btn>
            <v-btn
              color="secondary"
              variant="text"
              icon
              @click="openDuplicateDialog(row.item)"
            >
              <v-icon>mdi-content-copy</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Duplicar</v-tooltip
              >
            </v-btn>
            <v-btn
              color="error"
              variant="text"
              icon
              @click="openRemoveDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Remover</v-tooltip
              >
            </v-btn>
          </td>
        </tr>
      </template>
    </v-data-table-virtual>

    <v-dialog
      v-model="descDialog"
      @afterLeave="selectedPeriod = null"
      width="auto"
    >
      <v-card class="mx-auto" width="400">
        <v-card-title class="bg-info d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-text-box-outline</v-icon>
          <span class="text-h6">Descrição</span>
        </v-card-title>
        <v-card-text>
          <v-textarea
            :model-value="selectedPeriod.period_description"
            readonly
            no-resize
            rows="5"
            color="secondary"
            density="compact"
            variant="outlined"
          ></v-textarea>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="deleteDialog"
      @afterLeave="selectedPeriod = null"
      width="auto"
      persistent
    >
      <v-card>
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover Período</span>
        </v-card-title>
        <v-card-text>
          {{
            selectedPeriod
              ? `Deseja remover o período ${selectedPeriod.period_name}?`
              : ""
          }}
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDelete"
            @click="deletePeriod()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <PeriodEdit
      :period="selectedPeriod"
      :dialog="editDialog"
      @update:period="(value) => (selectedPeriod = value)"
      @update:dialog="(value) => (editDialog = value)"
      @refreshPeriods="$emit('refresh')"
    />
    <PeriodDuplicate
      ref="PeriodDuplicate"
      :period_id="selectedPeriod?.period_id"
      :dialog="duplicateDialog"
      @update:dialog="(value) => (duplicateDialog = value)"
      @refreshPeriods="$emit('refresh')"
    ></PeriodDuplicate>
  </v-sheet>
</template>

<script>
import PeriodEdit from "@/components/Periods/PeriodEdit.vue";
import PeriodDuplicate from "./PeriodDuplicate.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "PeriodsTable",
  components: {
    PeriodEdit,
    PeriodDuplicate,
  },
  props: {
    periods: {
      type: Array,
      required: true,
    },
    isLoadingPeriods: {
      type: Boolean,
      required: true,
    },
    hasMore: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      descDialog: false,
      editDialog: false,
      deleteDialog: false,
      duplicateDialog: false,
      isLoadingDelete: false,
      selectedPeriod: null,
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 260;
    },
    headers() {
      let headers = [
        {
          title: "",
        },
        {
          title: "Nome",
          key: "period_name",
        },
        {
          title: "Tipo",
          key: "periodtype_name",
        },
        {
          title: "Data de Início",
          key: "period_startdate",
        },
        {
          title: "Data de Fim",
          key: "period_enddate",
        },
        {
          title: "Descrição",
        },
        // {
        //   title: "",
        // },
      ];
      if (this.accountStore.hasRole(["Administrador"])) {
        headers.push({
          title: "",
        });
      }
      return headers;
    },
    tableItems() {
      return this.periods;
    },
  },
  methods: {
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.$emit("loadMore");
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    showInfo(periodId) {
      this.$emit("showInfo", periodId);
    },
    showDescription(period) {
      this.selectedPeriod = period;
      this.descDialog = true;
    },
    editPeriod(period) {
      this.selectedPeriod = Object.assign({}, period);
      this.editDialog = true;
    },
    openDuplicateDialog(period) {
      this.selectedPeriod = period;
      this.duplicateDialog = true;

      // this.selectedPeriod.period_id = null;
      // this.selectedPeriod.period_name = `${period.period_name} (Cópia)`;
      // this.editDialog = true;
    },
    openRemoveDialog(period) {
      this.selectedPeriod = period;
      this.deleteDialog = true;
    },
    async deletePeriod() {
      this.isLoadingDelete = true;
      await axios
        .request({
          url: "api/periods/" + this.selectedPeriod.period_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.deleteDialog = false;
          this.generalStore.successSnackbar("Período removido com sucesso!");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
