<template>
  <v-container>
    <v-row>
      <v-col col="12">
        <v-card levation="8">
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="2">
                <v-text-field
                  clearable
                  v-model="filter.name"
                  label="Nome"
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-autocomplete
                  clearable
                  v-model="filter.periodtype_id"
                  :items="periodtypes"
                  item-title="periodtype_name"
                  item-value="periodtype_id"
                  label="Tipo de Período"
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  :loading="isLoadingTypes"
                ></v-autocomplete>
              </v-col>
              <v-col cols="2">
                <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  :disabled="isLoadingPeriods"
                  @click="refreshPeriods()"
                >
                  <v-icon size="x-large">mdi-magnify</v-icon>
                </v-btn>
              </v-col>
              <v-spacer></v-spacer>
              <v-col v-if="accountStore.hasRole(['Administrador'])" cols="2">
                <AddPeriod @refresh="refreshPeriods()"></AddPeriod>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <PeriodDrawer ref="PeriodDrawer"></PeriodDrawer>
    <v-row>
      <v-col>
        <PeriodsTable
          :periods="periods"
          :isLoadingPeriods="isLoadingPeriods"
          :hasMore="hasMore"
          @loadMore="getPeriods()"
          @refresh="refreshPeriods()"
          @showInfo="showInfo($event)"
        ></PeriodsTable>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AddPeriod from "@/components/Periods/AddPeriod.vue";
import PeriodsTable from "@/components/Periods/PeriodsTable.vue";
import PeriodDrawer from "@/components/Periods/PeriodDrawer.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "PeriodsView",
  components: {
    AddPeriod,
    PeriodsTable,
    PeriodDrawer,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoadingPeriods: false,
      periods: [],

      filter: {
        name: null,
        periodtype_id: null,
      },
      limit: 12,
      offset: 0,
      hasMore: false,

      periodtypes: [],
      isLoadingTypes: false,
    };
  },
  methods: {
    async getPeriods() {
      this.isLoadingPeriods = true;
      await axios
        .request({
          url: "api/periods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            name: this.filter.name,
            periodtype_id: this.filter.periodtype_id,
            //pagination
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          this.periods.push(...response.data.periods);
          this.hasMore = response.data.hasMore;
          this.offset += response.data.periods.length;
          this.isLoadingPeriods = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar os períodos.");
          this.isLoadingPeriods = false;
        });
    },
    refreshPeriods() {
      this.periods = [];
      this.offset = 0;
      this.getPeriods();
    },
    showInfo(periodId) {
      this.$refs.PeriodDrawer.openDrawer(periodId);
    },

    async getPeriodTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/periodtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.periodtypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de período");
          this.isLoadingTypes = false;
        });
    },
  },
  mounted() {
    this.getPeriods();
    this.getPeriodTypes();
  },
};
</script>

<style></style>
