<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title class="d-flex justify-space-between align-center">
      <span class="text-h4">Informação Geral</span>
    </v-card-title>
    <v-card-text class="fill-height mt-5 scrollable-card">
      <v-sheet border rounded class="pa-5">
        <v-row>
          <v-col cols="6"
            ><span class="text-subtitle-1">
              Nome do Período:
              <strong>{{ localPeriod.period_name }}</strong></span
            ></v-col
          >
          <v-col cols="6"
            ><span class="text-subtitle-1">
              Tipo de Período:
              <v-chip label color="info">{{
                localPeriod.period_type.periodtype_name
              }}</v-chip>
            </span></v-col
          >
          <v-col cols="12">
            <span class="text-subtitle-1"> Descrição: </span>
            <br>
            <span v-if="localPeriod.period_description">
              {{ localPeriod.period_description }}
            </span>
            <span v-else class="text-overline text-center">
              Sem descrição
            </span>
          </v-col>
          <v-col cols="6">
            <v-locale-provider locale="pt">
              <v-date-picker
                title="Data de Início"
                :model-value="localPeriod.period_startdate"
                disabled
              ></v-date-picker>
            </v-locale-provider>
          </v-col>
          <v-col cols="6">
            <v-locale-provider locale="pt">
              <v-date-picker
                title="Data de término"
                :model-value="localPeriod.period_enddate"
                disabled
              ></v-date-picker>
            </v-locale-provider>
          </v-col>
        </v-row>
      </v-sheet>
    </v-card-text>
  </v-card>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

export default {
  name: "PeriodDetail",
  props: {
    period: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      localPeriod: { ...this.period },
    };
  },
  mounted() {
    this.localPeriod.period_startdate = new Date(this.period.period_startdate);
    this.localPeriod.period_enddate = new Date(this.period.period_enddate);
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 90%;
  overflow: auto;
}
</style>
