<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title>
      <span class="text-h4">Pesquisa</span>
      <v-row class="d-flex align-center justify-space-between">
        <v-col cols="5">
          <v-text-field
            v-model="filter.region_name"
            color="secondary"
            placeholder="Nome"
            density="compact"
            variant="solo"
            hide-details
            class="mt-5"
            clearable
            append-icon="mdi-magnify"
            @click:append="refreshRegions"
            @click:clear="refreshRegions"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-card-text class="scrollable-card mt-5">
      <v-progress-linear
        v-if="isLoading"
        color="primary"
        indeterminate
      ></v-progress-linear>
      <v-data-iterator
        v-else
        :items="regions"
        item-value="regioninperiod_id"
        :items-per-page="regions.length"
      >
        <template v-slot:default="{ items, isExpanded, toggleExpand }">
          <v-row>
            <v-col
              cols="12"
              v-for="item in items"
              :key="item.raw.regioninperiod_id"
            >
              <v-card>
                <v-card-title class="d-flex align-center justify-space-between">
                  <span clas="text-h4"> {{ item.raw.region.region_name }}</span>
                  <v-btn
                    v-if="
                      accountStore.hasRole([
                        'Produtor',
                        'Organização',
                        'Administrador',
                      ])
                    "
                    color="error"
                    variant="text"
                    icon
                    @click="openDeleteDialog(item.raw)"
                  >
                    <v-icon>mdi-delete-outline</v-icon>
                    <v-tooltip activator="parent" location="bottom">
                      Remover
                    </v-tooltip>
                  </v-btn>
                </v-card-title>
                <div class="px-4">
                  <v-switch
                    :label="`${
                      isExpanded(item) ? 'Esconder' : 'Mostrar'
                    } Detalhes`"
                    :model-value="isExpanded(item)"
                    density="compact"
                    color="secondary"
                    @click="() => toggleExpand(item)"
                  ></v-switch>
                  <v-expand-transition>
                    <div v-if="isExpanded(item)" class="mb-5">
                      <PeriodRegionDetail
                        :regionId="item.raw.region.region_id"
                      ></PeriodRegionDetail>
                    </div>
                  </v-expand-transition>
                </div>
              </v-card>
            </v-col>
            <v-col cols="12" v-if="hasMore">
              <v-skeleton-loader
                v-intersect.quiet="{
                  handler: loadMore,
                  options: {
                    threshold: 0.3,
                  },
                }"
                type="card"
              ></v-skeleton-loader>
            </v-col>
            <v-col v-else cols="12">
              <br />
            </v-col>
          </v-row>
        </template>
      </v-data-iterator>
    </v-card-text>
    <v-dialog
      v-model="deleteDialog"
      persistent
      width="auto"
      @afterLeave="resetDeleteDialog"
    >
      <v-card width="40rem">
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover perído da região</span></v-card-title
        >
        <v-card-text class="text-center">
          <span
            >Tem a certeza que quer dessassociar a região
            <b>{{ regioninperiod.region.region_name }}</b
            >?</span
          >
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDelete"
            @click="deleteRegionPeriod()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import PeriodRegionDetail from "./PeriodRegionDetail.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "PeriodRegions",
  components: { PeriodRegionDetail },
  props: {
    period: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoading: false,
      regions: [],
      filter: {
        region_name: "",
      },
      limit: 5,
      offset: 0,
      hasMore: false,

      regioninperiod: null,
      deleteDialog: false,
      isLoadingDelete: false,
    };
  },

  methods: {
    async getRegions() {
      //   await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/periods/" + this.period.period_id + "/regionsinperiods",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          params: {
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          let _regions = response.data.regioninperiods.map((regioninperiod) => {
            return {
              regioninperiod_id: regioninperiod.regioninperiod_id,
              region: regioninperiod.region,
            };
          });
          this.regions.push(..._regions);
          this.offset += this.regions.length;
          this.hasMore = response.data.hasMore;
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar regiões do período");
        });
    },
    loadMore(isIntersecting) {
      if (isIntersecting && this.hasMore) {
        this.getRegions();
      }
    },
    refreshRegions() {
      this.regions = [];
      this.offset = 0;
      this.getRegions();
    },

    openDeleteDialog(regioninperiod) {
      this.regioninperiod = regioninperiod;
      this.deleteDialog = true;
    },

    resetDeleteDialog() {
      this.regioninperiod = null;
      this.deleteDialog = false;
    },
    deleteRegionPeriod() {
      this.isLoading = true;
      axios
        .request({
          url: "api/regionsinperiods/" + this.regioninperiod.regioninperiod_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.isLoading = false;
          this.refreshRegions();
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao remover região do período");
        });
    },
  },
  mounted() {
    this.isLoading = true;
    this.getRegions();
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 90%;
  overflow: auto;
}
</style>
